.select {
  margin-bottom: 15px;
}

.select label {
  margin: 0 0 3px 0;
  padding: 0;
  font-weight: 700;
  display: block;
}

.select select {
  display: block;
  box-sizing: border-box;
  border: 1px solid #bebebe;
  border-radius: 10px;
  margin: 0 0 5px;
  height: 30px;
  width: 100%;
  outline: none;
  transition: all 300ms ease-in-out;
}