.quizList {
  display: flex;
  justify-content: center;
  padding-top: 100px;
  flex-grow: 1;
  width: 100%;
  background: #328acd;
}

.quizList h1 {
  color: #fff;
}

.quizList ul {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}

.quizList li {
  margin-bottom: 10px;
}

.quizList li a {
  text-decoration: none;
  color: #fff;
}

.quizList li a:hover{
  color: #363f54;
}